@import url('https://fonts.googleapis.com/css2?family=Alex+Brush&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto&family=Spectral&display=swap');

*{
    --white: #e9e5d9;
    --tan: #c9bfb5;
    --darkertan: #d1b496;
    --pinkish: #c18c84;
    --burntorange: #c47044;
    --brown: #a25636;
    --blue: #3b4658;
}

#root{
    padding: 0;
    margin: 0;
}

html, body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

body{
    height: 100vh;
}

footer{
    min-height: 25%;
}

.footerContainer{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}


h1{
    color: red; 
    
}

a{
    
    text-decoration: none;
}

nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    width: 100vw;
    background-color: white;
    /* padding: 1.5rem; */
    box-shadow: 1px 2px 8px  lightgrey;
    position: fixed;
    z-index: 3;

}

nav a{
    text-decoration: none;
    color: #0f2429;
    
    opacity: 0.4;
    font-weight: 100;
    font-size: 1.5rem;
    font-family: 'Spectral';
    
}

nav a:hover{
    opacity: 0.6;
}

nav ul{
    list-style-type: none;
    text-align: center;
    margin: 0;
    padding: 0;
}

ul{
    height: 100%;
    display: flex;
    gap: 2rem;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.list{
    height: 100%;
    padding: 1.5rem;
}

.logo{
    display: inline-block;
    padding: 1.5rem;
}

.text{
    position: relative;
    font-size: 1.5em;
    font-weight: 600;
    letter-spacing: 10px;
    font-family: 'Alex Brush';
    color: var(--pinkish);
}



.text::before{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: white;
    border-left: 2px solid;
    animation:
        animate 6s steps(22) 0.3s forwards;
        
}





@keyframes animate{
    40%, 60%{
        left: 100%
    }

    50%{
        width: 1%;
    }
    60%{
        width: 1%;
    }
    70%{
        width: 0%;
    }
    80%{
        width: 0%;
    }
    90%{
        width: 0%;
    }
   
    100%{
        left: 0%;
        width: 0%;
        position: relative
    }
    
   
} 

.svgContainer{
    
    display: flex;
    flex-wrap: wrap;
    background-image: url(/public/images/writing.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 80vh;
    width: 100%;
    z-index: -1;
}

.contactContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-image: url(/public/images/calligraphy.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 100vh;
    width: 100%;
    z-index: -1;
}


.formDiv{
    display: flex;
    /* height: 100%;
    width: 100%; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* margin-top: 50vh; */
    min-height: 100%;
    min-width: 100%;
    border-color: transparent;
    border-radius: 4px;
    border-style: solid;
    box-sizing: border-box;
    
    /* background-color: rgba(193, 140, 132, 0.5)!important; */
    background-color: rgba(59, 70, 88, 0.5)!important;
    padding: 1.5rem;
    
}

.contactHeader{
    color: #fff;
    font-size: 2.5rem;
    font-weight: 200;
    font-family: 'Alex Brush';
    width: 100%;
    text-align: center;
    
}

.contactHeader h3{
    letter-spacing: 5px;
    line-height: normal;
}

.contactClass{
    width: 100%;
    
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    justify-content: space-between;
    row-gap: 3rem;
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

