    height: auto;
    line-height: 21px!important;
    margin-bottom: 0;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    padding: 10px 10px!important;
    width: 100%!important;
    box-sizing: border-box!important;
    max-width: 100%!important;
    transition: 0.5s
}

#subscribe:hover{
    opacity: 0.92;
    cursor: pointer;
    background-color: var(--tan);
}

